<script>
import { Line as LineChartGenerator } from 'vue-chartjs/legacy'
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  LineElement,
  LinearScale,
  CategoryScale,
  PointElement
} from 'chart.js'

ChartJS.register(
  Title,
  Tooltip,
  Legend,
  LineElement,
  LinearScale,
  CategoryScale,
  PointElement
)

export default {
  components: {
    LineChartGenerator
  },
  data() {
    return {
      loaded: false,
      totalDate: 0,
      studyDate: 0,
      averageStudyTime: 0,
      chartData: {
        labels: [],
        datasets: [
          {
            label: ' ()',
            backgroundColor: '#f87979',
            data: []
          }
        ]
      },
      chartOptions: {
        responsive: true,
        maintainAspectRatio: false
      },
    };
  },
  methods: {
    moveProjectDetail(name) {
      console.log(name)
      this.$router.push("/projects/" + name)
    },
    padTo2Digits(num) {
      return num.toString().padStart(2, '0');
    },
    toHoursAndMinutes(totalMinutes) {
      const minutes = totalMinutes % 60;
      const hours = Math.floor(totalMinutes / 60);

      return `${this.padTo2Digits(hours)} ${this.padTo2Digits(minutes)} `;
    },
  },
  async mounted() {
    this.loaded = false
    try {
      await this.axios
        .get("https://mogakco.parkh.today/api/users/1")
        .then((result) => {
          this.chartData.labels = result.data.data.total.dateList
          this.chartData.datasets[0].data = result.data.data.total.studyTimeList
          this.totalDate = result.data.data.total.totalDate
          this.studyDate = result.data.data.total.studyDate
          this.averageStudyTime = result.data.data.total.averageStudyTime
        })
      this.loaded = true
    } catch (e) {
      console.error(e)
    }
  },
  props: {
    chartId: {
      type: String,
      default: 'line-chart'
    },
    datasetIdKey: {
      type: String,
      default: 'label'
    },
    width: {
      type: Number,
      default: 400
    },
    height: {
      type: Number,
      default: 400
    },
    cssClasses: {
      default: '',
      type: String
    },
    styles: {
      type: Object,
      default: () => { }
    },
    plugins: {
      type: Array,
      default: () => []
    }
  },
};
</script>

